* {
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body {
  background-color: #242424;
  color:white;
}

.app {
  width:100%;
  height:100vh;
  display:flex;
  justify-content:center;
  align-items:Center;
  flex-direction: column;

}

form {
  display:flex;
  align-items:center;
}

.add-todo-input {
  width:400px;
  height:40px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background:#dfdfdf;
  border:none;
  padding-left:10px;
  outline:none;
}

.add-todo-btn {
  width:120px;
  height:40px;
  font-size:18px;
  background:#47d65f;
  border:none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  color:white;
}

.todo-list {
  width:600px;
  height:auto;
  min-height:50px;
  padding:10px;
  border-radius:10px;
  border:1px solid gray;
  list-style: none;
}

.todo-list .todo-item {
  width:100%;
  min-height:50px;
  height:auto;
  background-color: #333333;
  padding-left:10px;
  padding-right:10px;
  border-radius:10px;
  display:Flex;
  align-items:center;
  font-size:18px;
  margin-top:5px;
  margin-bottom:5px;
  display:grid;
  grid-column-gap: 20px;
  grid-template-columns: 10px 10px auto 20px; 
}

.done {
  text-decoration: line-through;
}

.todo-list .todo-item .delete-btn {
  padding:.5rem 1em;
  background: #db3333;
  color:white;
  border:none;
  display:flex;
  justify-content:center;
  align-items:center;
  border-radius:5px;
  cursor:pointer;
}

.loader-animation {
  display:flex;
  justify-content: center;
  margin:.5rem
}

.lds-dual-ring,
.lds-dual-ring:after {
  box-sizing: border-box;
}
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6.4px solid currentColor;
  border-color: currentColor transparent currentColor transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

